import { Component, OnInit, ViewChild} from '@angular/core';
import { OrderService } from '../order.service';
import { MatPaginator, MatTableDataSource } from '@angular/material';
import {MatSort} from '@angular/material/sort';


@Component({
  selector: 'app-orderlist',
  templateUrl: './orderlist.component.html',
  styleUrls: ['./orderlist.component.css']
})


export class OrderlistComponent implements OnInit {

  private orderList;

  displayedColumns: string[] = ['select', 'orderNo', 'dateNeeded', 'billingName', 'customerCode', 'poNo', 'statusCode'];
  
  //datasource = this.orderList;

  datasource =new MatTableDataSource<OrderListElement>();

  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;

  constructor( private orderService: OrderService ) { 
    
  }

  

 
  ngOnInit() {

    this.orderService.getOrderList((res) => {
      this.orderList = res;
      this.datasource =new MatTableDataSource<OrderListElement>(res);
      this.datasource.paginator = this.paginator;
      this.datasource.sort = this.sort;
    });

    

    //this.datasource.paginator = this.paginator;
   // this.orderList = this.orderService.orderListValue;

  }

  applyFilter(filterValue: string) {
    this.datasource.filter = filterValue.trim().toLowerCase();
  }

  openOrder() {
    console.log("User pressed open button");
  }

}

export interface OrderListElement {
  orderNo: string;
  dateNeeded: string;
  billingName: string;
  customerCode: string;
  poNo: string;
  statusCode: string;

}
